body {
  font-family: 'Nunito', sans-serif;
  color: #1e1e1e; }

* {
  box-sizing: border-box; }

a {
  text-decoration: none;
  display: inline-block;
  color: initial; }

p {
  display: inline-block; }

.container {
  width: 90%;
  display: block;
  margin: 0 auto; }

.text-grey {
  color: #999999; }

.text-dark-grey {
  color: #444; }

.text-black {
  color: #1e1e1e; }

.text-orange {
  color: #ecbc4b; }

.text-white {
  color: #fff; }

.text-bold {
  font-weight: 900; }

.text-thin {
  font-weight: 100; }

.text-regular {
  font-weight: 400; }

.title {
  color: #1e1e1e;
  font-size: 1.930em;
  margin: 3px 0; }

.subtitle {
  color: #999;
  font-size: 1.175em;
  margin: 3px 0; }

.section-title {
  font-size: 1em;
  text-transform: uppercase; }

.text-field {
  border: none;
  background: #fff;
  box-shadow: 0px 3px 11px -5px rgba(0, 0, 0, 0.9);
  padding: 15px;
  width: 100%;
  border-radius: 15px;
  margin: 10px auto;
  outline: none; }

.button {
  color: #fff;
  background: linear-gradient(to right, #ffb6b6 0%, #ef9625 100%);
  text-transform: uppercase;
  padding: 15px 25px;
  border-radius: 25px;
  border: none;
  box-shadow: 0px 5px 34px -5px #f39557;
  letter-spacing: 5px; }

.inverse-button {
  background: transparent;
  border: 1px solid #ef767a;
  color: #ef767a;
  text-transform: uppercase;
  padding: 15px 25px;
  border-radius: 25px;
  letter-spacing: 5px; }

.error-message {
  color: #ef767a;
  margin-left: 5%; }

.message {
  border-radius: 10px;
  color: #fff;
  padding: 3px;
  text-align: center; }
  .message.error {
    background: #ef767a; }
  .message.success {
    background: #5aed71; }

header {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin: 3vh 0; }
  header .logo {
    height: 40px;
    width: 40px; }
  header .close-button {
    height: 20px;
    width: 20px;
    outline: none;
    user-select: none; }
    header .close-button::selection {
      background: none; }

body.home .greetings {
  margin: 15% 0; }

body.home .login-form .login-button {
  display: block;
  margin: 10px auto;
  width: 140px; }

body.home .functions {
  margin-top: 10vh;
  text-align: center; }

body.cadastro .register-form {
  margin-top: 10vh; }

body.cadastro .register-button {
  width: 192px;
  display: block;
  margin: 15px auto; }

body.cadastro .select {
  background: #fff;
  color: #999; }

body.dashboard .school-rank .school {
