body.quiz {
  background: #efefff; }
  body.quiz .header-background {
    background: url("../img/quiz-background.png") no-repeat;
    background-size: cover;
    height: 200px;
    position: fixed;
    left: 0;
    width: 100%;
    box-shadow: 0px 5px 23px 0px #f1d166; }
  body.quiz .question-num {
    font-size: .75em;
    text-align: center;
    text-transform: uppercase;
    width: 100%;
    letter-spacing: 2px; }
  body.quiz .question-ask {
    align-items: center;
    font-size: 1em;
