    letter-spacing: 2px;
    line-height: 20px;
    height: 120px;
    display: flex;
    width: 100%; }
  body.quiz .asks-container {
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    background: #fff;
    border-radius: 10px;
    margin-top: -20px; }
    body.quiz .asks-container .ask-button {
      background: #fff;
      border: 1px solid #efefef;
      box-sizing: border-box;
      border-radius: 35px;
      color: #999999;
      padding: 20px 0;
      width: 80%;
      margin: 10px auto; }
      body.quiz .asks-container .ask-button:first-child {
        margin-top: 20px; }
    body.quiz .asks-container .timer-wrapper {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center; }
      body.quiz .asks-container .timer-wrapper figure {
        margin: 30px 2%; }
      body.quiz .asks-container .timer-wrapper .timer-bar {
        background: #f0ebe6;
        border-radius: 10px;
        height: 15px;
        width: 67%;
        overflow: hidden; }
      body.quiz .asks-container .timer-wrapper .actived {
        position: relative; }
        body.quiz .asks-container .timer-wrapper .actived:before {
          content: '';
          position: absolute;
          top: 0;
